:root {
  --main-font: "DM Sans", sans-serif;
  --title-font: "Titan One", cursive;
  --accent-cl: #d41443;
  --pink-cl: #ffa5ba;
  --white-cl: #fff;
  --black-cl: #000;
  --about-cl: #907e82;
  --pink-bg-cl: #ffb8ca;
  --light-pink-bg-cl: #fff5f6;
  --decoration-cl: #c7133e;
  --btn-accent-cl: #d41342;
  --error-cl: #e22f37;
  --all-ok-cl: #ffb8ca;
  --warning-cl: #936e1e;
  --items: 1;
  --indent: 30px;
  --transition-duration: 400ms;
  --transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
