.modal-form__group {
  margin-bottom: 20px;
  display: block;
  position: relative;
}

.modal-form__label {
  color: var(--accent-cl);
  font-size: 14px;
  display: inline-block;
  position: relative;
}

.modal-form__label:before {
  content: "*";
