  justify-content: flex-start;
  align-items: center;
  margin-right: 20px;
  font-size: 10px;
  display: flex;
  position: absolute;
  bottom: -22px;
  left: 50px;
}

.modal-form__alert--error {
  color: var(--error-cl);
}

.modal-form__alert--warning {
