}

.modal-form__alert__icon {
  fill: currentColor;
  opacity: 0;
  transition: fill var(--transition-duration) var(--transition-timing-function);
  display: inline-flex;
  position: absolute;
  bottom: 12px;
  right: 10px;
  transform: scale(.3);
}

.modal-form__alert-icon--warning {
  fill: var(--warning-cl);
}

.modal-form__alert-icon--error {
  fill: var(--error-cl);
}

.modal-form__input:focus ~ .modal-form__alert--warning, .modal-form__input:focus ~ .modal-form__alert-icon--warning {
  letter-spacing: .01em;
  opacity: 1;
  transform: scale(1);
}

.modal-form__input:not(:placeholder-shown) ~ .modal-form__alert--warning, .modal-form:not(:placeholder-shown) ~ .modal-form__alert-icon--warning {
